html,
body {
  height: 100%;
}

body {
  background: #808080;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-family: Helvetica Neue;
}

h1 {
  font-size: 2em;
  color: #eee;
  display: inline-block;
}

a {
  color: #fff;
}

p {
  margin-top: 1em;
  text-align: center;
  color: #aaa;
}

label {
  color: #00f;
}

body {
  background-image: url("https://image.ibb.co/eAwC9p/Omar.png");
  background-repeat: no-repeat;
  background-size: auto;
  background-position: center center;
}

input.text {
  position: relative;
  top: 146px;
  width: 93px;
  left: -9px;
  opacity: 0.6;
  background: #fff;
  border-radius: 20px;
  height: 27px;
  font-size: 15px;
  font-weight: bold;
  text-align: center;
  outline: none;
}


